import React from "react";
import { useState } from "react";
const Lists = ({ item, handleRemove, index, fruits }) => {
  const [count, setCount] = useState(0);

  const handleCount = (e) => {
    setCount(parseInt(e.target.value));

    fruit.count = parseInt(e.target.value);
  };
  let fruit = fruits.find((fruit) => fruit.item === item);

  return (
    <div className="p-2 my-2 dark-border row text-center">
      <div className="col-3">
        {item}
        <div className="">Price: {fruit.price}</div>
      </div>
      <div className="col-3">
        <input
          min={0}
          type="number"
          className=""
          value={count}
          onChange={(e) => {
            handleCount(e);
          }}
        />
      </div>
      <div className="col-3">{fruit.price * count}</div>
      <div className="col-3">
        <button onClick={() => handleRemove(index)}>remove</button>
      </div>
    </div>
  );
};

export default Lists;
