import Fruits from "./Fruits";
import Lists from "./Lists";
import Result from "./Result";
import { useState } from "react";
import { Link } from "react-router-dom";

const Shop = ({ currentUser, fruits }) => {
  const [selectedFruit, setSelectedFruit] = useState("apple");

  const [items, setItems] = useState([]);

  const handleAdd = () => {
    for (var key in items) {
      if (items[key] === selectedFruit) {
        console.log("dupe");
        return;
      } else {
      }
    }

    setItems([...items, selectedFruit]);
  };
  const handleRemove = (index) => {
    const newItems = items.filter((item) => item !== items[index]);
    console.log(newItems);
    setItems(newItems);
  };
  //   const [transaction, setTransaction] = useState([]);
  const calculateItems = () => {
    let total = 0;
    for (var key in fruits) {
      total += fruits[key].price * fruits[key].count;
    }
    // setTransaction([{ name: currentUser }, { items: items }, { total: total }]);

    fetch("https://react-mongo-lloyd.herokuapp.com/api/transactions", {
      headers: { "Content-Type": "application/json" },
      method: "POST",
      body: JSON.stringify({
        name: currentUser.name,
        items: items,
        total: total,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        fetch(
          "https://react-mongo-lloyd.herokuapp.com/api/transactions/" +
            data["data"].insertedId
        )
          .then((res) => res.json())
          .then((data) => setResult(data["data"]));
      });
  };

  const [result, setResult] = useState(null);

  return (
    <div className="shop">
      <div className="h4 d-block">Hello {currentUser.name}</div>
      <Fruits
        fruits={fruits}
        selectedFruit={selectedFruit}
        setSelectedFruit={setSelectedFruit}
      />
      <button onClick={() => handleAdd(selectedFruit)}>Add</button>
      {items.length > 0 ? (
        <div className="row text-center">
          <div className="col-3 h5">Item</div>
          <div className="col-3 h5">Count</div>
          <div className="col-3 h5">Total Price</div>
          <div className="col-3 h5"></div>
        </div>
      ) : (
        ""
      )}
      {items.map((item, index) => {
        return (
          <Lists
            key={index}
            item={item}
            index={index}
            handleRemove={handleRemove}
            fruits={fruits}
          />
        );
      })}

      {items.length > 0 ? <button onClick={calculateItems}>Buy</button> : ""}

      <Link to={{ pathname: "/" }}>
        <button>Logout</button>
      </Link>
      {result !== null ? <Result results={result} /> : ""}
    </div>
  );
};

export default Shop;
