import Fruit from "./Fruit";
const Fruits = ({ fruits, selectedFruit, setSelectedFruit }) => {
  return (
    <>
      <label className="h5">Fruits</label>
      <br></br>
      <select
        className="mx-2"
        value={selectedFruit}
        onChange={(e) => {
          setSelectedFruit(e.target.value);
        }}
      >
        {fruits.map((fruits, index) => (
          <Fruit key={index} fruits={fruits} />
        ))}
      </select>
    </>
  );
};

export default Fruits;
