import { useState } from "react";
import { Link } from "react-router-dom";

const Login = ({ handleLogin }) => {
  const [emailInputLogin, setEmailInputLogin] = useState("");
  const [passInputLogin, setPassInputLogin] = useState("");
  return (
    <div className="">
      <div className="">
        <h2>Login</h2>
        <label className="h5">Email</label>
        <input
          type="text"
          className="form-control"
          placeholder="Email"
          value={emailInputLogin}
          onChange={(e) => {
            setEmailInputLogin(e.target.value);
          }}
        />
        <label className="h5">Password</label>
        <input
          type="password"
          className="form-control"
          placeholder="Password"
          value={passInputLogin}
          onChange={(e) => {
            setPassInputLogin(e.target.value);
          }}
        />
        <Link to={{ pathname: "./register" }}>
          Don't have an account? Register now!
        </Link>

        <button
          className="btn text-white login-register-button my-2 d-block"
          onClick={(e) => {
            handleLogin(emailInputLogin, passInputLogin);
          }}
        >
          Login
        </button>
      </div>
    </div>
  );
};

export default Login;
