import { useState } from "react";
import { Link } from "react-router-dom";

const Register = ({ handleRegister }) => {
  const [emailInput, setEmailInput] = useState("");
  const [passInput, setPassInput] = useState("");
  const [nameInput, setNameInput] = useState("");

  return (
    <div className="">
      <h2>Register</h2>
      <label className="h5">Email</label>
      <input
        type="email"
        className="form-control"
        placeholder="Email"
        value={emailInput}
        onChange={(e) => {
          setEmailInput(e.target.value);
        }}
      />

      <label className="h5">Password</label>
      <input
        type="password"
        className="form-control"
        placeholder="Password"
        value={passInput}
        onChange={(e) => {
          setPassInput(e.target.value);
        }}
      />

      <label className="h5">Name</label>
      <input
        type="text"
        className="form-control"
        placeholder="Name"
        value={nameInput}
        onChange={(e) => {
          setNameInput(e.target.value);
        }}
      />
      <Link to={{ pathname: "/" }}>Have an account? Login now!</Link>
      <Link to={{ pathname: "" }}>
        <button
          className="btn text-white login-register-button my-2 d-block"
          onClick={() => {
            handleRegister(emailInput, passInput, nameInput);
          }}
        >
          Register
        </button>
      </Link>
      <button
        onClick={() => {
          console.log(emailInput, passInput, nameInput);
        }}
      >
        test
      </button>
    </div>
  );
};

export default Register;
