import Login from "./components/Login";
import Register from "./components/Register";
import Shop from "./components/Shop";
import "./App.css";
import { useState, useEffect } from "react";
import { HashRouter as Router, Routes, Route } from "react-router-dom";

function App() {
  const [fruits, setFruits] = useState([]);
  useEffect(() => {
    fetch("https://react-mongo-lloyd.herokuapp.com/api/items")
      .then((result) => result.json())
      .then((data) => setFruits(data["data"]));
  }, []);
  const [currentUser, setCurrentUser] = useState("");

  const handleRegister = (email, pass, name) => {
    console.log(email, pass, name);
    fetch("https://react-mongo-lloyd.herokuapp.com/api/users/", {
      headers: { "Content-Type": "application/json" },
      method: "POST",
      body: JSON.stringify({
        name: name,
        pass: pass,
        email: email,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        console.log(data);
      });
  };

  const handleLogin = (email, pass) => {
    console.log(email, pass);
    fetch("https://react-mongo-lloyd.herokuapp.com/api/users/login", {
      headers: { "Content-Type": "application/json" },
      method: "POST",
      body: JSON.stringify({ email: email, pass: pass }),
    })
      .then((res) => res.json())
      .then((data) => {
        data["data"] === null ? handleFailedLogin() : handleSuccessLogin(data);
      });
  };
  const handleFailedLogin = () => {
    console.log("no account found");
    setCurrentUser(null);
  };

  const handleSuccessLogin = (data) => {
    setCurrentUser(data["data"]);
    window.location.replace("./#/shop");
  };

  return (
    <Router>
      <div className="container-sm w-75 my-2 p-3 dark-border">
        <Routes>
          <Route path="/" element={<Login handleLogin={handleLogin} />} />
          <Route
            path="/register"
            element={<Register handleRegister={handleRegister} />}
          />
          <Route
            path="/shop"
            element={<Shop currentUser={currentUser} fruits={fruits} />}
          />
        </Routes>
      </div>
    </Router>
  );
}

export default App;
