import React from "react";

const Result = ({ results }) => {
  return (
    <div>
      <h3>Transaction:</h3>
      <h5>Name: {results.userName}</h5>
      <ol>
        {results !== null
          ? results.items.map((result, index) => {
              return <li key={index}>{result}</li>;
            })
          : ""}
      </ol>
      <h5>Total Price: {results.price}</h5>
    </div>
  );
};

export default Result;
